import React from 'react'
import { Link } from 'react-router-dom'

const Cards = ({name, id ,type}) => {
    const imgCard = `/assets/${type}-${id}.png`
    return (
        <div className="card m-3 col-12 col-md-4 bg-secondary" style={{
            width: "200px",
        }}>
        <img loading='lazy' src={imgCard} className="card-img-top" alt={id} />
        <div className="card-body text-center">
          <h5 className="card-title text-ligth">{name}</h5>
          <Link to={`/character/${id}`} className="btn btn-primary">Ver Mas...</Link>
        </div>
      </div> 
      )
}

export default Cards
