import React from 'react'
import { Characters } from '../Characters'
import Cards from '../components/Cards'


const MenScreem = () => {

    const mans = Characters.filter(men => men.type === "h") 

    return (
        <div className="container mt-3">
            <h1 className='text-center'>Mans</h1>
            <hr />
         <div className='row justify-content-center'>
            {
                mans.map((men)=>
                    <Cards key={men.id}{...men}/>
                )
            }
        </div>
        </div>
     
    )
}

export default MenScreem
